import { SimidProtocol } from './simid_protocol.js';
// import { SimidPlayer} from '../player/simid_player'
/*
 * A subclass of a SIMID ad that implements functionality that will
 * be the same for all simid ads.
 */
export class BaseSimidCreative {
  constructor() {
    /**
     * Data about the creative, not known until after init.
     * @protected {?Object}
     */
    this.creativeData = null;

    /**
     * Data about the environment the creative plays in, not known until after init.
     * @protected {?Object}
     */
    this.environmentData = null;

    /**
     * The most recent video state from the player.
     * @protected {?Object}
     */
    this.videoState = {
      currentSrc:'',
      currentTime: -1, // Time not yet known
      duration: -1, // duration unknown
      ended: false,
      muted: false,
      paused: false,
      volume: 0.5,
      fullscreen: false
    }


    /**
     * The simid version, once the player makes it known.
     * @protected {String}
     */
    this.simidVersion = '';

    /**
     * The protocol for sending and receiving messages.
     * @protected {!SimidProtocol}
     */
    this.simidProtocol = new SimidProtocol();

    this.addListeners_();
  }

  /**
   * Sets up the creative to listen for messages from the player
   * @private
   */
  addListeners_() {
    this.simidProtocol.addListener(this.simidProtocol.PlayerMessage.INIT, this.onInit.bind(this));
    this.simidProtocol.addListener(this.simidProtocol.PlayerMessage.START_CREATIVE, this.onStart.bind(this));
    this.simidProtocol.addListener(this.simidProtocol.PlayerMessage.FATAL_ERROR, this.onFatalError.bind(this));
    this.simidProtocol.addListener(this.simidProtocol.PlayerMessage.AD_STOPPED, this.onAdStopped.bind(this));
    this.simidProtocol.addListener(this.simidProtocol.PlayerMessage.AD_SKIPPED, this.onAdSkipped.bind(this));
    this.simidProtocol.addListener(this.simidProtocol.PlayerMessage.LOG, this.onReceivePlayerLog.bind(this));
    this.simidProtocol.addListener(this.simidProtocol.PlayerMessage.RESIZE, this.onReceiveResize.bind(this));
    // Handlers with different video events.
    this.simidProtocol.addListener(this.simidProtocol.MediaMessage.DURATION_CHANGE, this.onDurationChange.bind(this));
    this.simidProtocol.addListener(this.simidProtocol.MediaMessage.ENDED, this.onVideoEnded.bind(this));
    this.simidProtocol.addListener(this.simidProtocol.MediaMessage.ERROR, this.onVideoError.bind(this));
    this.simidProtocol.addListener(this.simidProtocol.MediaMessage.PAUSE, this.onPause.bind(this));
    this.simidProtocol.addListener(this.simidProtocol.MediaMessage.PLAY, this.onPlay.bind(this));
    this.simidProtocol.addListener(this.simidProtocol.MediaMessage.PLAYING, this.onPlaying.bind(this));
    this.simidProtocol.addListener(this.simidProtocol.MediaMessage.SEEKED, this.onSeeked.bind(this));
    this.simidProtocol.addListener(this.simidProtocol.MediaMessage.SEEKING, this.onSeeking.bind(this));
    this.simidProtocol.addListener(this.simidProtocol.MediaMessage.TIME_UPDATE, this.onTimeUpdate.bind(this));
    this.simidProtocol.addListener(this.simidProtocol.MediaMessage.VOLUME_CHANGE, this.onVolumeChange.bind(this));
  }

  ready() {
    console.log('BaseSimidCreative :: ready');
    this.simidProtocol.createSession();

  }

  /**
   * Receives init message from the player.
   * @param {!Object} eventData Data from the event.
   * @protected
   */
  onInit(eventData) {
    console.log('BaseSimidCreative :: onInit');
    this.updateInternalOnInit(eventData);
    this.simidProtocol.resolve(eventData, {});
  }

  /**
   * Updates internal data on initialization call.
   *
   * Note: When overriding the onInit function and not wishing
   * to always resolve, subclasses may instead use this function.
   * @param {!Object} eventData Data from the event.
   * @protected
   */
  updateInternalOnInit(eventData) {
    console.log('BaseSimidCreative :: updateInternalOnInit');
    this.creativeData = eventData.args.creativeData;
    this.environmentData = eventData.args.environmentData;
    this.videoState.muted = this.environmentData.muted;
    this.videoState.volume = this.environmentData.volume;
    console.log(' - creativeData: ');
    console.log(this.creativeData);

    console.log(' - environmentData: ');
    console.log(this.environmentData);
  }

  /**
   * Receives start message from the player.
   * @param {!Object} eventData Data from the event.
   * @protected
   */
  onStart(eventData) {
    console.log('BaseSimidCreative :: onStart');
    // Acknowledge that the ad is started.
    this.simidProtocol.resolve(eventData, {});
    console.log('Simid creative started.')
  }

  /**
   * Called when the creative receives the fatal error message from the player.
   * @protected
   */
  onFatalError(eventData) {
    // After resolving the iframe with this ad should be cleaned up.
    this.simidProtocol.resolve(eventData, {});
  }

  /**
   * Called when the creative receives the stop message from the player.
   * @protected
   */
  onAdStopped(eventData) {
    console.log('BaseSimidCreative :: onAdStopped');
    // After resolving the iframe with this ad should be cleaned up.
    this.simidProtocol.resolve(eventData, {});
  }

  /**
   * Called when the creative receives the skip message from the player.
   * @protected
   */
  onAdSkipped(eventData) {
    console.log('BaseSimidCreative :: onAdSkipped');
    // After resolving the iframe with this ad should be cleaned up.
    this.simidProtocol.resolve(eventData, {});
  }

  /**
   * Called when the creative receives a resize message from the player.
   * @param {!Object} eventData Data from the event.
   * @protected
   */
  onReceiveResize(eventData) {
    console.log('BaseSimidCreative :: onReceiveResize');
    this.environmentData.creativeDimensions = eventData.args.creativeDimensions;
    this.environmentData.videoDimensions = eventData.args.videoDimensions;
  }

  /** 
   * Opens the click through url and lets the player know about it.
   * @protected
   */
  clickThru() {
    console.log('BaseSimidCreative :: clickThru');
    console.log(this.creativeData.clickThroughUrl);
    let link = this.creativeData.clickThroughUrl
    window.open(link)
  }

  /**
   * Asks the player for the state of the video element.
   * @protected
   */
  fetchMediaState() {
    console.log('BaseSimidCreative :: fetchMediaState');
    this.simidProtocol.sendMessage(this.simidProtocol.CreativeMessage.GET_MEDIA_STATE, {})
        .then((data) => this.onGetMediaStateResolve(data));
  }

  /**
   * @protected
   */
  onGetMediaStateResolve(data) {
    console.log('BaseSimidCreative :: onGetMediaStateResolve');
    this.videoState = data;
  }

  /**
   * @protected
   */
  onDurationChange(data) {
    this.videoState.duration = data.args.duration;
  }

  /**
   * @protected
   */
  onVideoEnded() {
    console.log('BaseSimidCreative :: onVideoEnded');
    this.videoState.ended = true;
  }

  /**
   * @protected
   */
  onVideoError() {
    // no op for this example
  }

  /**
   * @protected
   */
  onPause() {
    console.log('BaseSimidCreative :: onPause');
    this.videoState.paused = true;
  }

  /**
   * @protected
   */
  onPlay() {
    console.log('BaseSimidCreative :: onPlay');
    this.videoState.paused = false;
  }

  /**
   * @protected
   */
  onPlaying() {
    console.log('BaseSimidCreative :: onPlaying');
    this.videoState.paused = false;
  }

  /**
   * @protected
   */
  onSeeked() {
    // no op for this example
  }

  /**
   * @protected
   */
  onSeeking() {
    // no op for this example
  }

  /**
   * @protected
   */
  onTimeUpdate(data) {

    this.videoState.currentTime = data.args.currentTime;
  }

  /**
   * @protected
   */
  onVolumeChange(data) {
    this.videoState.volume = data.args.volume;
  }

  onReceivePlayerLog(data) {
    const logMessage = data.args.message;
    console.log("Received message from player: " + logMessage);
  }

  /**
   * Sends message requesting to resize creative based off of given resizeParameters
   * @param {!Object} resizeParams An object with the video & creative dimensions.
   */
  requestResize(resizeParams) {
    console.log('BaseSimidCreative :: requestResize');
    this.simidProtocol.sendMessage(this.simidProtocol.CreativeMessage.REQUEST_RESIZE, resizeParams).then( () => {
      this.environmentData.creativeDimensions = resizeParams.creativeDimensions;
      this.environmentData.videoDimensions = resizeParams.videoDimensions;
    });
  }
}

