
import { Overlayer} from './overlayer';
import "./main.scss";

const overlayer = new Overlayer();
overlayer.ready();


const requestPauseBtn = document.getElementById('requestPause');
const requestPlayBtn = document.getElementById('requestPlay');
const requestStopBtn = document.getElementById('requestStop');
const requestVolumeBtn = document.getElementById('requestVolume');
const collapseNonlinearBtn = document.getElementById('collapseNonlinear');

requestPauseBtn.addEventListener('click', () => {
    console.log('SIMID: sending REQUEST_PAUSE');
    overlayer.simidProtocol.sendMessage(overlayer.simidProtocol.CreativeMessage.REQUEST_PAUSE);
});

requestPlayBtn.addEventListener('click', () => {
    console.log('SIMID: sending REQUEST_PLAY');
    overlayer.simidProtocol.sendMessage(overlayer.simidProtocol.CreativeMessage.REQUEST_PLAY);
});

requestStopBtn.addEventListener('click', () => {
    console.log('SIMID: sending REQUEST_STOP');
    overlayer.simidProtocol.sendMessage(overlayer.simidProtocol.CreativeMessage.REQUEST_STOP);
});

requestVolumeBtn.addEventListener('click', () => {
    console.log('SIMID: sending REQUEST_VOLUME');
    overlayer.simidProtocol.sendMessage(overlayer.simidProtocol.CreativeMessage.REQUEST_VOLUME);
});

collapseNonlinearBtn.addEventListener('click', () => {
    console.log('SIMID: sending COLLAPSE_NONLINEAR');
    overlayer.simidProtocol.sendMessage(overlayer.simidProtocol.CreativeMessage.COLLAPSE_NONLINEAR);
});

